.figmaland__content-main {
  /* text-align: center; */
  /* position: relative; */
  /* z-index: -1; */
  display: flex;
  width: 100vw;
  height: 400px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  /* background: chartreuse; */
}
.figmaland__content-heading {
  font-family: Graphik;
  font-style: normal;
  font-weight: 500;
  font-size: 5rem;
  line-height: 5rem;
  width: 50vw;
  /* or 110% */
  text-align: center;
  letter-spacing: 0.2px;

  /* light text */
  color: #ffffff;
}
.figmaland__content-content {
  width: 40vw;
  font-family: Graphik;
  font-style: normal;
  font-weight: normal;
  font-size: 1.5rem;
  /* line-height: 1.5rem; */

  /* or 150% */
  text-align: center;
  letter-spacing: 0.2px;
}
.figmaland__content-button {
  width: 40vw;
  display: flex;
  justify-content: space-evenly;
}

@media screen and (max-width: 600px) {
  .figmaland__content-main {
    display: flex;
    justify-content: space-evenly;
    height: 50vh;
    /* position: absolute; */
    /* z-index: 1; */
  }
  .figmaland__content-heading {
    font-family: Graphik;
    font-style: normal;
    font-weight: 500;
    font-size: 2rem;
    line-height: 2.2rem;
    width: 100vw;
    /* position: absolute; */
    /* or 110% */
    text-align: center;
    letter-spacing: 0.2px;

    /* light text */
    color: #ffffff;
  }
  .figmaland__content-content {
    width: 80vw;
    font-family: Graphik;
    font-style: normal;
    font-weight: normal;
    font-size: 1rem;
    line-height: 1.5rem;
    /* position: absolute; */

    /* or 150% */
    text-align: center;
    letter-spacing: 0.2px;
  }
  .figmaland__content-button {
    /* background-color: aqua; */
    width: 100vw;
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
}
@media screen and (max-width: 1200px) and (min-width: 601px) {
  .figmaland__content-main {
    display: flex;
    justify-content: space-evenly;
    height: 50vh;
    /* position: absolute; */
    /* z-index: 1; */
  }
  .figmaland__content-heading {
    font-family: Graphik;
    font-style: normal;
    font-weight: 500;
    font-size: 3rem;
    line-height: 4rem;
    width: 100vw;
    /* position: absolute; */
    /* or 110% */
    text-align: center;
    letter-spacing: 0.2px;

    /* light text */
    color: #ffffff;
  }
  .figmaland__content-content {
    width: 80vw;
    font-family: Graphik;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5rem;
    line-height: 2rem;
    /* position: absolute; */

    /* or 150% */
    text-align: center;
    letter-spacing: 0.2px;
  }
  .figmaland__content-button {
    /* background-color: aqua; */
    width: 100vw;
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
}
